// Dependencies - Vendor.
import { array, boolean, literal, nullable, number, object, optional, picklist, record, string } from 'valibot';

const STATUS_COLOR_IDS = ['amber', 'green', 'red', 'other'] as const;
const statusColorIdSchema = picklist(STATUS_COLOR_IDS);

const COMPONENT_STATUS_IDS = ['alpha', 'beta', 'generalAvailability', 'notApplicable', 'preAlpha', 'proposed', 'releaseCandidate', 'unavailable', 'underReview'] as const;
const componentStatusIdSchema = picklist(COMPONENT_STATUS_IDS);

const COMPONENT_TYPE_IDS = [
    'app',
    'connector',
    'connectorConnection',
    'context',
    'contextModelGroup',
    'contextModel',
    'contextModelDimensionGroup',
    'contextModelDimension',
    'contextModelDimensionHierarchy',
    'contextModelEntityGroup',
    'contextModelEntity',
    'contextModelEntityDataItem',
    'contextModelEntityEvent',
    'contextModelEntityPrimaryMeasure',
    'contextModelSecondaryMeasureGroup',
    'contextModelSecondaryMeasure',
    'dataView',
    'dimension',
    'engine',
    'eventQuery',
    'presenter',
    'presenterPresentation',
    'tool'
] as const;
const componentTypeIdSchema = picklist(COMPONENT_TYPE_IDS);

const LOCALE_CODES = ['en-au', 'en-gb', 'en-us', 'es-es'] as const;
const localeCodeSchema = picklist(LOCALE_CODES);

const localisedStringSchema = record(localeCodeSchema, string());

const componentStatusSchema = object({
    id: string(),
    color: statusColorIdSchema,
    label: string()
});

const componentConfigEntries = {
    id: string(),
    label: record(string(), string()),
    description: record(string(), string()),
    firstCreatedAt: optional(number()),
    icon: optional(string()),
    iconDark: optional(string()),
    lastUpdatedAt: optional(nullable(number())),
    status: optional(nullable(componentStatusSchema)),
    statusId: componentStatusIdSchema,
    typeId: componentTypeIdSchema
};

const MODULE_TYPE_IDS = ['app', 'engine', 'connector', 'context', 'presenter', 'tool'] as const;
const moduleTypeIdSchema = picklist(MODULE_TYPE_IDS);

const connectorCategorySchema = object({
    id: string(),
    label: string()
});

const moduleConfigEntries = {
    ...componentConfigEntries,
    typeId: moduleTypeIdSchema,
    version: string()
};

const CONNECTOR_AUTH_METHOD_IDS = ['apiKey', 'disabled', 'oAuth2', 'none'] as const;
const connectorAuthMethodIdSchema = picklist(CONNECTOR_AUTH_METHOD_IDS);

export const connectorImplementationSchema = object({
    activeConnectionCount: optional(number()),
    canDescribe: optional(boolean()),
    id: optional(string()),
    authMethodId: connectorAuthMethodIdSchema,
    label: optional(localisedStringSchema),
    maxConnectionCount: optional(number()),
    params: optional(array(record(string(), string())))
});

const CONNECTOR_MODULE_CATEGORY_IDS = ['application', 'curatedDataset', 'database', 'fileStore'] as const;
const connectorModuleCategoryIdSchema = picklist(CONNECTOR_MODULE_CATEGORY_IDS);

const CONNECTOR_OPERATION_IDS = [
    'abortOperation',
    'authenticateConnection',
    'createObject',
    'describeConnection',
    'dropObject',
    'findObject',
    'getRecord',
    'listNodes',
    'previewObject',
    'removeRecords',
    'retrieveRecords',
    'upsertRecords'
] as const;
const connectorOperationSchema = picklist(CONNECTOR_OPERATION_IDS);

const CONNECTOR_USAGE_IDS = ['bidirectional', 'destination', 'source', 'unknown'] as const;
const connectorUsageIdSchema = picklist(CONNECTOR_USAGE_IDS);

export const connectorConfigSchema = object({
    ...moduleConfigEntries,
    category: optional(nullable(connectorCategorySchema)),
    categoryId: connectorModuleCategoryIdSchema,
    implementations: record(string(), connectorImplementationSchema),
    operations: array(connectorOperationSchema),
    typeId: literal('connector'),
    usageId: connectorUsageIdSchema,
    vendorAccountURL: optional(nullable(string())),
    vendorDocumentationURL: optional(nullable(string())),
    vendorHomeURL: optional(nullable(string()))
});
